.chatMessage {
  max-width: 500px;
  height: fit-content;
  padding: 10px;
  margin-left: 15px;
  border-radius: 10px;
  color: #fff;
  margin-top: 5%;
  display: flex;
  align-items: center;
}

.messageTextContainer{
    padding: 10px;
    border-radius: 20px;
    background-color: rgb(44, 44, 44);
}

.sent {
  flex-direction: row-reverse;
  margin-left: 60%;
}

.sent .messageTextContainer {
  background-color: #2c4072;  
  align-self: flex-end;
}

.chatMessage img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: 0 10px 0 10px;
}

.chatMessage p {
  font-family: Poppins;
  height: fit-content;
  font-weight: 200;
}
