@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #e4e4e4;
}


.appContainer{
  width: 600px;
  height: 900px;
  margin: auto;
  margin-top: 15px;
  background-color: #222222;
  box-shadow: 0px 8px 24px 0px #00000015;
}

.signInContainer{
    width: 65%;
    height: 20%;
    margin: auto;
    padding-top: 55%;
}

.signInContainer h1{
    text-align: center;
    font-family: Montserrat;
    font-weight: 400;
    color: #ffffff;
}

.signInContainer button{
    margin: auto;
    margin-top: 5%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 65%;
    height: 30%;
    line-height: 30px;
    border: none;
    border-radius: 25px;
    outline: none;
    background: #fff;
    transition: all .15s ease-in-out;
    cursor: pointer;
}


.signInContainer button img{
    padding: 5%;
}

.signInContainer button p{
    padding: 5%;
    font-family: Poppins;
    font-size: 18px;
    font-weight: 400;
}
.channel{
    height: 775px;
    width: 100%;
    overflow-y: auto;
}

.channel::-webkit-scrollbar {
    width: 10px;
  }
  
  .channel::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  }
  
  .channel::-webkit-scrollbar-thumb {
    background-color: #494949;
    border-radius: 5px;
    outline: none;
  }
  
  .channel::-webkit-scrollbar-thumb:hover{
      background-color: #5e5e5e;    
      cursor: pointer;
  }
.chatMessage {
  max-width: 500px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 10px;
  margin-left: 15px;
  border-radius: 10px;
  color: #fff;
  margin-top: 5%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.messageTextContainer{
    padding: 10px;
    border-radius: 20px;
    background-color: rgb(44, 44, 44);
}

.sent {
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  margin-left: 60%;
}

.sent .messageTextContainer {
  background-color: #2c4072;  
  -webkit-align-self: flex-end;  
          align-self: flex-end;
}

.chatMessage img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: 0 10px 0 10px;
}

.chatMessage p {
  font-family: Poppins;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  font-weight: 200;
}

header{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    background-color: #262626;
}

.backChat{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.backChat h1{
    font-family: Montserrat;
    font-weight: 400;
    font-size: 42px;
    margin-top: 0.2vh;
    padding-bottom: 0.2vh;
    color: #fff;
}

.backChat span{
    font-size: 60px;
    color: #fff;
}

.signOutBtn{
    height: 30px;
    width: 70px;
    margin-top: 2.5%;
    margin-left: 55%;
    border: none;
    border-radius: 25px;
    outline: none;
    background: #1a1a1a;
    color: #fff;
    font-family: Poppins;
    font-size: 12px;
    font-weight: 300;
    cursor: pointer;
    transition: all .15s ease-in-out;
}

.signOutBtn:hover{
    background-color: #2B4997;
}

.textArea {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.textArea form {
  width: 100%;
}

.textArea textarea {
  width: 81.5%;
  height: 50px;
  resize: none;
  font-family: Poppins;
  color: #fff;
  padding: 1%;
  background: none;
  outline: none;
  border: 1px solid #ffffff10;
}

.textArea form button {
  width: 97px;
  height: 65px;
  position: absolute;
  border: none;
  background: #2c4072;
  outline: none;
  color: #fff;
  font-family: Poppins;
  font-size: 40px;
  font-weight: 300;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}

.textArea form button:hover {
  background-color: #2b4997;
}

.textArea textArea::-webkit-scrollbar {
  width: 10px;
}

.textArea textArea::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.textArea textArea::-webkit-scrollbar-thumb {
  background-color: #494949;
  border-radius: 5px;
  outline: none;
}

.textArea textArea::-webkit-scrollbar-thumb:hover{
    background-color: #5e5e5e;    
    cursor: pointer;
}

