.textArea {
  width: 100%;
  display: flex;
  flex-direction: row;
}

.textArea form {
  width: 100%;
}

.textArea textarea {
  width: 81.5%;
  height: 50px;
  resize: none;
  font-family: Poppins;
  color: #fff;
  padding: 1%;
  background: none;
  outline: none;
  border: 1px solid #ffffff10;
}

.textArea form button {
  width: 97px;
  height: 65px;
  position: absolute;
  border: none;
  background: #2c4072;
  outline: none;
  color: #fff;
  font-family: Poppins;
  font-size: 40px;
  font-weight: 300;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}

.textArea form button:hover {
  background-color: #2b4997;
}

.textArea textArea::-webkit-scrollbar {
  width: 10px;
}

.textArea textArea::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.textArea textArea::-webkit-scrollbar-thumb {
  background-color: #494949;
  border-radius: 5px;
  outline: none;
}

.textArea textArea::-webkit-scrollbar-thumb:hover{
    background-color: #5e5e5e;    
    cursor: pointer;
}
