header{
    display:flex;
    flex-direction: row;
    background-color: #262626;
}

.backChat{
    display:flex;
    flex-direction: row;
}

.backChat h1{
    font-family: Montserrat;
    font-weight: 400;
    font-size: 42px;
    margin-top: 0.2vh;
    padding-bottom: 0.2vh;
    color: #fff;
}

.backChat span{
    font-size: 60px;
    color: #fff;
}

.signOutBtn{
    height: 30px;
    width: 70px;
    margin-top: 2.5%;
    margin-left: 55%;
    border: none;
    border-radius: 25px;
    outline: none;
    background: #1a1a1a;
    color: #fff;
    font-family: Poppins;
    font-size: 12px;
    font-weight: 300;
    cursor: pointer;
    transition: all .15s ease-in-out;
}

.signOutBtn:hover{
    background-color: #2B4997;
}
