.signInContainer{
    width: 65%;
    height: 20%;
    margin: auto;
    padding-top: 55%;
}

.signInContainer h1{
    text-align: center;
    font-family: Montserrat;
    font-weight: 400;
    color: #ffffff;
}

.signInContainer button{
    margin: auto;
    margin-top: 5%;
    display: flex;
    flex-direction: row;
    width: 65%;
    height: 30%;
    line-height: 30px;
    border: none;
    border-radius: 25px;
    outline: none;
    background: #fff;
    transition: all .15s ease-in-out;
    cursor: pointer;
}


.signInContainer button img{
    padding: 5%;
}

.signInContainer button p{
    padding: 5%;
    font-family: Poppins;
    font-size: 18px;
    font-weight: 400;
}